@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #17232a;
  color: #ffffff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*font-family: 'VT323', monospace;*/

/* Customize your main colors in :root variables */
:root {
  --main-background-color: #0c4113;
  --card-background-color: #529a26;
  --countdown-background-color: #433765;
  --main-text-color: #f7f6f4;
  --title-text-color: #61f039;
  --main-color-bg: #17232a;
}

body {
  background-color: #17232a;
  background-color: var(--main-color-bg);
  background: url(/static/media/cubes2.be80b83e.png);
  background-size: auto;
  font-family: "VT323", monospace;
  font-size: 1.1em;
}

h2,
p {
  color: #f7f6f4;
  color: var(--main-text-color);
}

h3 {
  font-weight: normal;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  /* border-radius: 6px;*/
  background-color: #0c4113;
  background-color: var(--main-background-color);
  border: none;
}

::-webkit-scrollbar-thumb {
  border-radius: 6px;
  border: none;
  background-color: #386a19;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #529a26;
  background-color: var(--card-background-color);
}

.ISuper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 7rem 0rem;
}

.ISuper img {
  width: 80%;
  box-shadow: 0 0 20px black;
  border: 3px solid var(--card-background-color);
  border-radius: 15px;
}

.MsgSpan {
  color: var(--title-text-color);
  text-shadow: 0 0 10px black;
  font-size: 30px;
}

